import React from "react";
import { ApplicationStates } from "../../../interfaces/application";
import { ApplicationPeriodInterface } from "../../../shared/interfaces";
import VBarChart from "./VBarChart";

type ApplicationsStatusProps = {
  FC_ApplicationsByInsitutes: (status?: "ALL" | ApplicationStates) => {
    data: ApplicationPeriodInterface[];
    ids: string[];
    uids: string[];
    positions: string[];
  };
  chartHeight?: number;
};

const ApplicationsStatus: React.FC<ApplicationsStatusProps> = (props) => {
  const thisSeries: {
    name: string;
    data: number[];
  }[] = [
    {
      name: "Initiated",
      data: [
        props.FC_ApplicationsByInsitutes(ApplicationStates.Initiated).data
          .length,
      ],
    },
    {
      name: "Incomplete",
      data: [
        props.FC_ApplicationsByInsitutes(ApplicationStates.Incomplete).data
          .length,
      ],
    },
    {
      name: "Rejected",
      data: [
        props.FC_ApplicationsByInsitutes(ApplicationStates.Rejected).data
          .length,
      ],
    },
    {
      name: "Pending",
      data: [
        props.FC_ApplicationsByInsitutes(ApplicationStates.Pending).data.length,
      ],
    },
    {
      name: "Shortlisted",
      data: [
        props.FC_ApplicationsByInsitutes(ApplicationStates.Shortlisted).data
          .length,
      ],
    },
  ];

  const statusApplications = {
    // labels: string[];
    categoriesLabel: [
      "Initiated",
      "Incomplete",
      "Rejected",
      "Pending",
      "Shortlisted",
    ],
    dataLabels: false,
    yaxisText: "(Number of Applications)",
    tooltipText: "applications",
    series: thisSeries,
  };

  return (
    <section className="my-3 bg-white">
      <div className="flex items-center px-3">
        <span className="text-gray-600 font-semibold text-xl py-4 px-6">
          Applications per Status
        </span>
      </div>
      <div className="mb-4 mx-2">
        <VBarChart
          labels={statusApplications.categoriesLabel}
          categoriesLabel={statusApplications.categoriesLabel}
          dataLabels={statusApplications.dataLabels}
          yaxisText={statusApplications.yaxisText}
          tooltipText={statusApplications.tooltipText}
          series={statusApplications.series}
          colors={["#A5978B", "#546E7A", "#D7263D", "#E2C044", "#008FFB"]}
          chartHeight={props.chartHeight}
        />
      </div>
    </section>
  );
};

export default React.memo(ApplicationsStatus);
