import React, { useState } from "react";
import { IoIosRemove } from "react-icons/io";
import { ApplicationPeriodInterface } from "../../shared/interfaces";
import { calculate_age } from "../../utils/functions";
import HBarChart from "./ChartComponents/HBarChart";
import PieChart from "./ChartComponents/PieChart";

export const genderColor = { male: "#3998a2", female: "#002243" };

type ChartGenderProps = {
  getAllApplicantByInsitute: (gender?: "ALL" | "M" | "F") => {
    data: ApplicationPeriodInterface[];
    ids: string[];
  };
};

const ChartGender: React.FC<ChartGenderProps> = (props) => {
  const rangeConfig: {
    from: number;
    to: number;
  } = { from: 18, to: 75 };
  const [ageFrom, setAgeFrom] = useState<string>(rangeConfig.from.toString());
  const [ageTo, setAgeTo] = useState<string>(rangeConfig.to.toString());
  const [rangeChanged, setRangeChanged] = useState<boolean>(false);
  const handleAgeRange = (thisRange: string, TYPE: "FROM" | "TO" = "FROM") => {
    // let thisAge:number= thisRange
    if (TYPE === "FROM") {
      setAgeFrom(thisRange);
      if (Number(thisRange) > Number(ageTo)) {
        setAgeTo(thisRange);
      }
    } else {
      setAgeTo(thisRange);
      if (Number(thisRange) < Number(ageFrom)) {
        setAgeFrom(thisRange);
      }
    }
    setRangeChanged(true);
  };

  const ageRange = (start: number, end: number): number[] => {
    let result: number[] = [];
    for (let index = start; index <= end; index++) {
      result.push(index);
    }
    return result;
  };

  const FC_FilterByAge = (
    userData: ApplicationPeriodInterface[],
    from: string,
    to: string
  ): {
    data: ApplicationPeriodInterface[];
    ids: string[];
  } => {
    let ids: string[] = [],
      data: ApplicationPeriodInterface[] = userData;
    data = userData.filter(
      (item) =>
        calculate_age(item.userInfo.user_info.date_of_birth) >= Number(from) &&
        calculate_age(item.userInfo.user_info.date_of_birth) <= Number(to)
    );
    return { data, ids };
  };

  return (
    <section className="grid grid-cols-1 md:grid-cols-3 gap-2 my-3">
      {/* APPLICANTS BY GENDER ========================================= */}
      <div className="bg-white rounded col-span-1 md:col-span-1">
        <div className="px-3 py-4">
          <span className="text-gray-600 font-semibold text-xl">
            Number of Applicants per Gender
          </span>
        </div>
        <div className="flex justify-center">
          <PieChart
            options={{
              labels: ["Male", "Female"],
              legend: {
                position: "bottom",
                horizontalAlign: "center",
                fontSize: "16px",
                itemMargin: {
                  horizontal: 3,
                  vertical: 0,
                },
              },
              plotOptions: {
                pie: {
                  donut: {
                    labels: {
                      show: true,
                      name: {
                        show: true,
                        fontSize: "22px",
                        fontFamily: "Rubik",
                        color: "#dfsda",
                        offsetY: -10,
                      },
                      value: {
                        show: true,
                        fontSize: "16px",
                        fontFamily: "Helvetica, Arial, sans-serif",
                        color: undefined,
                        offsetY: 16,
                        formatter: function (val) {
                          return val;
                        },
                      },
                      total: {
                        show: true,
                        label: "Total",
                        color: "#373d3f",
                        formatter: function (w) {
                          return w.globals.seriesTotals.reduce(
                            (a: number, b: number) => {
                              return a + b;
                            },
                            0
                          );
                        },
                      },
                    },
                  },
                },
              },
              colors: [genderColor.male, genderColor.female],
            }}
            series={[
              FC_FilterByAge(
                props.getAllApplicantByInsitute("M").data,
                ageFrom,
                ageTo
              ).data.length,
              FC_FilterByAge(
                props.getAllApplicantByInsitute("F").data,
                ageFrom,
                ageTo
              ).data.length,
            ]}
          />
        </div>
      </div>
      <div className="bg-white rounded col-span-1 md:col-span-2">
        <div className="flex items-center justify-between px-3">
          <span className="text-gray-600 my-4 font-semibold text-xl">
            Number of Applicants per Age
          </span>
          <div className="bg-white">
            <nav className="flex flex-col sm:flex-row">
              <select
                value={ageFrom}
                onChange={(e) => handleAgeRange(e.target.value)}
                className="mt-1 block w-full py-2 px-3 border-2 border-gray-300 bg-white rounded focus:outline-none focus:border-gray-500 sm:text-sm"
              >
                {ageRange(rangeConfig.from, rangeConfig.to).map(
                  (item, index) => (
                    <option value={item} key={index + item}>
                      {item}
                    </option>
                  )
                )}
              </select>
              {/* <div className="flex-1">
              </div> */}
              <div className="relative self-center">
                <IoIosRemove className="mt-2 h-6 w-6 text-gray-600" />
              </div>
              <select
                value={ageTo}
                onChange={(e) => handleAgeRange(e.target.value, "TO")}
                className="mt-1 block w-full py-2 px-3 border-2 border-gray-300 bg-white rounded focus:outline-none focus:border-gray-500 sm:text-sm"
              >
                {ageRange(rangeConfig.from, rangeConfig.to).map(
                  (item, index) => (
                    <option value={item} key={index + item}>
                      {item}
                    </option>
                  )
                )}
              </select>
              {/* <div className="flex-1">
              </div> */}
              {/* <button
                className={`py-4 px-6 block focus:outline-none border-b-2 font-medium ${
                  gender === "ALL"
                    ? "text-gray-600 border-blue-500 cursor-default animate__animated animate__flash"
                    : "hover:text-blue-500 text-blue-500"
                }`}
                onClick={() => gender !== "ALL" && setGender("ALL")}
              >
                All
              </button>
              <button
                className={`py-4 px-6 block focus:outline-none border-b-2 font-medium ${
                  gender === "F"
                    ? "text-gray-600 border-blue-500 cursor-default animate__animated animate__flash"
                    : "hover:text-blue-500 text-blue-500"
                }`}
                onClick={() => gender !== "F" && setGender("F")}
              >
                Female
              </button>
              <button
                className={`py-4 px-6 block focus:outline-none border-b-2 font-medium ${
                  gender === "M"
                    ? "text-gray-600 border-blue-500 cursor-default animate__animated animate__flash"
                    : "hover:text-blue-500 text-blue-500"
                }`}
                onClick={() => gender !== "M" && setGender("M")}
              >
                Male
              </button> */}
            </nav>
          </div>
        </div>
        <HBarChart
          getAllApplicantByInsitute={props.getAllApplicantByInsitute}
          stacked={true}
          ageFrom={Number(ageFrom)}
          ageTo={Number(ageTo)}
          action={rangeChanged}
          setAction={setRangeChanged}
        />
      </div>
      {/* END APPLICANTS BY GENDER ===================================== */}
    </section>
  );
};

export default React.memo(ChartGender);
